import useValidator from "../../../hook/validate";

function SaveResultEventValidate(value, action = false) {
    return useValidator(value, [
        useValidator.isRequired("buyer_name", "Tn khch hng khng c b trng"),
        useValidator.isRequired("buyer_phone", "S in thoi khch hng khng c b trng"),
        useValidator.isRequired("customer_id", "Khch hng khng c b trng"),
        useValidator.isNumber("turnover", "Khch hng khng c b trng"),
    ]);
}

const field = {
    buyer_name: "",
    buyer_phone: "",
    customer_id: "",
    turnover: "",
    consultant_id: "",
    status: 0,
    seller_name: ""
}


export { field };

export default SaveResultEventValidate;